@font-face {
  font-family: 'MullerLight';
  src: url('../fonts/Muller/FontfabricMullerLight.eot') format('embedded-opentype'),
  url('../fonts/Muller/FontfabricMullerLight.woff2') format('woff2'),
  url('../fonts/Muller/FontfabricMullerLight.woff') format('woff'),
  url('../fonts/Muller/FontfabricMullerLight.ttf') format('truetype'),
  url('../fonts/Muller/FontfabricMullerLight.svg') format('svg');
}

@font-face {
  font-family: 'MullerMedium';
  src: url('../fonts/Muller/FontfabricMullerMedium.eot') format('embedded-opentype'),
  url('../fonts/Muller/FontfabricMullerMedium.woff2') format('woff2'),
  url('../fonts/Muller/FontfabricMullerMedium.woff') format('woff'),
  url('../fonts/Muller/FontfabricMullerMedium.ttf') format('truetype'),
  url('../fonts/Muller/FontfabricMullerMedium.svg') format('svg');
}

@font-face {
  font-family: 'Lisa';
  src: url('../fonts/Lisa/Lisa.eot') format('embedded-opentype'),
  url('../fonts/Lisa/Lisa.woff2') format('woff2'),
  url('../fonts/Lisa/Lisa.woff') format('woff'),
  url('../fonts/Lisa/Lisa.ttf') format('truetype'),
  url('../fonts/Lisa/Lisa.svg') format('svg');
}

$MullerLight:'MullerLight';
$MullerMedium:'MullerMedium';
$Lisa:'Lisa';